import { ApiError, APIErrorResponse } from '@/types/repo';

/**
 *    API
 * @param message -    
 * @param status - HTTP  
 * @param info -    
 */
export const createApiError = (
  message: string,
  status: number,
  info: Partial<APIErrorResponse> = {}
): ApiError => {
  const error = new Error(message) as ApiError;
  error.status = status;
  error.info = {
    message,
    ...info
  };

  return error;
};
