import React from 'react';
import {
  Box,
  Typography,
  FormControl,
  Select,
  MenuItem,
  SelectChangeEvent,
  IconButton
} from '@mui/material';
import { ChevronLeft, ChevronRight } from '@mui/icons-material';
import type { SearchParams } from '../../../services/githubApi';
import {
  MAX_API_RESULTS,
  MAX_PAGES_WITH_DEFAULT_PER_PAGE
} from '../../../configs/constants';

import styles from '../RepositoryTable.module.scss';

interface TablePaginationProps {
  searchParams: SearchParams; //  
  totalCount: number; //   
  totalPages: number; //   
  onPageChange: (event: React.ChangeEvent<unknown>, page: number) => void; //   
  onPerPageChange: (event: SelectChangeEvent<number>) => void; //      
  isLoading: boolean; //    
}

/**
 *  
 */
const TablePagination: React.FC<TablePaginationProps> = ({
  searchParams,
  totalCount,
  totalPages,
  onPageChange,
  onPerPageChange,
  isLoading
}) => {
  //      
  const currentStart = (searchParams.page! - 1) * searchParams.per_page! + 1;
  const currentEnd = Math.min(
    searchParams.page! * searchParams.per_page!,
    totalCount
  );

  /**
   *     
   *     GitHub API   page-1
   */
  const handlePrevPage = () => {
    if (searchParams.page! > 1) {
      onPageChange({} as React.ChangeEvent<unknown>, searchParams.page! - 1);
    }
  };

  /**
   *     
   * -  1000  (MAX_API_RESULTS)
   * -  100   per_page=10 (MAX_PAGES_WITH_DEFAULT_PER_PAGE)
   */
  const handleNextPage = () => {
    if (
      searchParams.page! < Math.min(totalPages, MAX_PAGES_WITH_DEFAULT_PER_PAGE)
    ) {
      onPageChange({} as React.ChangeEvent<unknown>, searchParams.page! + 1);
    }
  };

  const canGoPrev = searchParams.page! > 1;
  const canGoNext =
    searchParams.page! < Math.min(totalPages, MAX_PAGES_WITH_DEFAULT_PER_PAGE);

  return (
    <Box className={styles.paginationContainer}>
      <Box className={styles.paginationInfo}>
        <Typography variant="body2" className={styles.paginationText}>
          Rows per page:
        </Typography>
        <FormControl size="small" className={styles.perPageSelect}>
          <Select
            value={searchParams.per_page}
            onChange={onPerPageChange}
            variant="outlined"
            disabled={isLoading}
          >
            <MenuItem value={10}>10</MenuItem>
            <MenuItem value={25}>25</MenuItem>
            <MenuItem value={50}>50</MenuItem>
          </Select>
        </FormControl>
        <Typography variant="body2" className={styles.paginationText}>
          {`${currentStart}-${currentEnd} of ${Math.min(
            totalCount,
            MAX_API_RESULTS
          )}`}
        </Typography>
      </Box>

      <Box className={styles.paginationControls}>
        <IconButton
          onClick={handlePrevPage}
          disabled={!canGoPrev || isLoading}
          size="small"
          className={styles.paginationButton}
        >
          <ChevronLeft />
        </IconButton>

        <IconButton
          onClick={handleNextPage}
          disabled={!canGoNext || isLoading}
          size="small"
          className={styles.paginationButton}
        >
          <ChevronRight />
        </IconButton>
      </Box>
    </Box>
  );
};

export default React.memo(TablePagination);
