import type {
  SearchRepositoriesResponse,
  Repository,
  SortOption,
  SortDirection
} from '../types/repo';
import {
  GITHUB_API_URL,
  DEFAULT_PER_PAGE,
  DEFAULT_RETRY_INTERVAL
} from '../configs/constants';
import { fetcher } from './fetcher';

export interface SearchParams {
  query: string; //  
  sort?: SortOption; //  
  order?: SortDirection; //  
  page?: number; //  
  per_page?: number; //   
}

/**
 *    SWR     
 */
export const createSearchKey = (params: SearchParams): string => {
  const {
    query,
    sort = 'stars',
    order = 'desc',
    page = 1,
    per_page = DEFAULT_PER_PAGE
  } = params;

  if (!query.trim()) {
    return '';
  }

  const searchParams = new URLSearchParams({
    q: query.trim(),
    sort,
    order,
    page: page.toString(),
    per_page: per_page.toString()
  });

  return `${GITHUB_API_URL}/search/repositories?${searchParams.toString()}`;
};

/**
 *        
 */
export const createRepositoryKey = (owner: string, repo: string): string => {
  return `${GITHUB_API_URL}/repos/${owner}/${repo}`;
};

/**
 *   GitHub
 * @param params  
 * @throws {ApiError}   API
 * @returns Promise   
 */
export const searchRepositories = async (
  params: SearchParams
): Promise<SearchRepositoriesResponse> => {
  const url = createSearchKey(params);
  if (!url) {
    throw new Error('  ');
  }

  return fetcher<SearchRepositoriesResponse>(url);
};

/**
 *     
 */
export const getRepository = async (
  owner: string,
  repo: string
): Promise<Repository> => {
  const url = createRepositoryKey(owner, repo);

  return fetcher<Repository>(url);
};

/**
 *   SWR
 *   (dedupingInterval)   
 */
export const swrConfig = {
  fetcher,
  revalidateOnFocus: false,
  revalidateOnReconnect: true,
  errorRetryCount: 3,
  errorRetryInterval: DEFAULT_RETRY_INTERVAL // 2   
};
